import Header from "../components/Header";
import { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import queryString from "query-string";
import styled from "styled-components";
import Pagination from "../components/Pagination";
import ClothesBox from "../components/ClothesBox";
import { useSelector, useDispatch } from "react-redux";
import { fetchProducts } from "../redux/clothes/productsSlice";
import Loading from "../components/Loading";

const Container = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

function KeywordPage() {
  const [category, setCategory] = useState();
  const dispatch = useDispatch();
  const { search } = useLocation();
  const { keyword } = queryString.parse(search);
  const [currentPage, setCurrentPage] = useState(1);
  const postPerPage = 21; //  
  //  
  const indexOfLastPage = currentPage * postPerPage; // 1 * 35 = 35 
  const indexOfFirstPage = indexOfLastPage - postPerPage; // 35 - 35 = 0 
  const [currentPosts, setCurrentPosts] = useState(category); //0~35 
  //   
  const paginate = (pageNum) => setCurrentPage(pageNum);

  const products = useSelector((state) => state.products);

  useEffect(() => {
    !products.data && dispatch(fetchProducts());
  }, []);

  useEffect(() => {
    if (!products.isLoading) {
      setCategory(products.data.filter((item) => item.name.includes(keyword)));
    }
  }, [keyword, products]);

  useEffect(() => {
    if (category) {
      setCurrentPosts(category.slice(indexOfFirstPage, indexOfLastPage));
    }
  }, [category, currentPage]);

  return (
    <Container>
      <Header />
      {products.isLoading ? (
        <Loading />
      ) : (
        <>
          <ClothesBox posts={currentPosts} />
          {currentPosts?.length > 0 && (
            <Pagination
              postPerPage={postPerPage}
              totalPosts={category?.length}
              paginate={paginate}
              currentPage={currentPage}
            />
          )}
        </>
      )}
    </Container>
  );
}
export default KeywordPage;
